$base-color-backgroud: rgba(0, 0, 0, 0.8);

.cnt-item{
    display: flex;
    flex-direction: row;
    border: 5px solid gray;
    border-radius: 10px;
    margin: 8px;
    background: $base-color-backgroud;
    
    &-img{
        width: 80px;
        height: 80px;
        margin: 10px;
        border: 5px solid gray;
        border-radius: 10px;
        img{
            width: 80px;
        }
    }
    &-body{
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 80px;
        margin: 10px;
        &-title{
            font-size: 24px;
            font-weight: 800;
            width: 100%;
        }
        &-prediction{
            font-size: 16px;
            font-weight: 400;
            width: 100%;
        }
    }
    
}